.prices-overlay {
  background-color: #282a2f;
}
.prices {
  background-color: #111319;
  background-image: url(../images/prices/prices-bg-mob.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  max-width: 1680px;
  margin: 0 auto;

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/prices/prices-bg-mob-2x.png);
  }

  @media screen and (min-width: 768px) {
    background-image: url(../images/prices/prices-bg-tab.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/prices/prices-bg-tab-2x.png);
    }
  }

  @media screen and (min-width: 1200px) {
    background-image: url(../images/prices/prices-bg.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/prices/prices-bg-2x.png);
    }
  }
}
//!
.prices__pre-text {
  margin-bottom: 20px;

  font-weight: 600;
  font-size: 11px;
  line-height: 15px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: rgba(157, 164, 189, 0.6);
}
//!
.prices__title {
  margin-bottom: 44px;

  font-weight: bold;
  font-size: 42px;
  line-height: 49px;
  letter-spacing: 0.05em;

  color: #ffffff;
}

.prices__list {
  margin-bottom: 50px;
  background: linear-gradient(180deg, #171a24 0%, rgba(23, 26, 36, 0.2) 100%);
  @media screen and (min-width: 1200px) {
    display: flex;
    background: unset;
  }

  //!
  .prices__list-item {
    width: 100%;
    padding: 30px 30px;
    @media screen and (min-width: 768px) {
      padding: 30px 40px;
    }
    @media screen and (min-width: 1200px) {
      width: 50%;
    }
  }
  .prices__item_side_left {
    padding-bottom: 0;
    margin-bottom: 20px;

    @media screen and (min-width: 1200px) {
      background-image: linear-gradient(180deg, #171a24 0%, rgba(23, 26, 36, 0.2) 100%);
      margin-bottom: unset;
      padding-bottom: 30px;
    }
  }

  .prices__item_side_right {
    padding-top: 0;
    @media screen and (min-width: 1200px) {
      background-image: linear-gradient(180deg, #090b13 0%, rgba(9, 11, 19, 0.2) 100%);

      padding-top: 30px;
    }
  }
}
//!
.prices__inner-item {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  &::before {
    content: '';
    height: 1px;
    flex-grow: 1;
    display: block;
    background-color: rgba(157, 164, 189, 0.12);
    flex-shrink: 0;
    margin-left: 6px;
    margin-right: 6px;
  }
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}
//!
.prices__item-text-descr {
  order: -1;
}
//!
.prices__item-text {
  font-size: 14px;
  line-height: calc(30 / 14);
  letter-spacing: 0.02em;
  color: var(--second-text-color);
  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: calc(30 / 18);
  }
}
//!
.prices__btn {
  margin: 0 auto;
  display: block;
}
